import React, { useState } from "react";

import "./BlogMain.css";

import { AiFillPlusCircle } from "react-icons/ai";

import { AiFillMinusCircle } from "react-icons/ai";

import { MdOpenInNew } from "react-icons/md";

export default function BlogMain() {
  const htmlToBeSet =
    '\n<h3 class="wp-block-heading">Introduction 321</h3>\n\n\n\n<p>In this article, we will examine how artificial intelligence (AI) is transforming the field of digital marketing and how it may aid companies in developing stronger relationships with their target markets while maximizing their marketing initiatives.</p>\n\n\n\n<h3 class="wp-block-heading">The Redefined Personalization</h3>\n\n\n\n<p>AI&#8217;s capacity to revolutionize tailored client experiences is one of the most important contributions it makes to digital marketing. Systems driven by AI can learn important information about people&#8217;s interests, behaviors, and previous contacts by analyzing enormous amounts of data. Armed with this insight, marketers can create hyper-personalized ads that connect with customers on a deeper level, increasing engagement.</p>\n\n\n\n<figure class="wp-block-image size-large"><img decoding="async" loading="lazy" width="1024" height="1024" src="https://dev-interac8digital.pantheonsite.io/wp-content/uploads/2023/07/Vector-1-7-1024x1024.png" alt="" class="wp-image-17" srcset="https://dev-interac8digital.pantheonsite.io/wp-content/uploads/2023/07/Vector-1-7-1024x1024.png 1024w, https://dev-interac8digital.pantheonsite.io/wp-content/uploads/2023/07/Vector-1-7-300x300.png 300w, https://dev-interac8digital.pantheonsite.io/wp-content/uploads/2023/07/Vector-1-7-150x150.png 150w, https://dev-interac8digital.pantheonsite.io/wp-content/uploads/2023/07/Vector-1-7-768x768.png 768w, https://dev-interac8digital.pantheonsite.io/wp-content/uploads/2023/07/Vector-1-7.png 1080w" sizes="(max-width: 1024px) 100vw, 1024px" /></figure>\n\n\n\n<h3 class="wp-block-heading">Customer care and chatbots</h3>\n\n\n\n<p>Artificial intelligence (AI)-driven chatbots have taken over websites and social media platforms, drastically changing the way that customers are supported. These virtual assistants are on call around the clock, responding quickly to client inquiries and taking care of issues at the moment. Chatbots not only speed up response times but also allow staff to concentrate on more difficult jobs, increasing productivity overall.</p>\n\n\n\n<figure class="wp-block-image size-full"><img decoding="async" loading="lazy" width="413" height="193" src="https://dev-interac8digital.pantheonsite.io/wp-content/uploads/2023/07/filezilla.png" alt="" class="wp-image-7" srcset="https://dev-interac8digital.pantheonsite.io/wp-content/uploads/2023/07/filezilla.png 413w, https://dev-interac8digital.pantheonsite.io/wp-content/uploads/2023/07/filezilla-300x140.png 300w" sizes="(max-width: 413px) 100vw, 413px"/></figure>\n';

  const [expand, setExpand] = useState(false);

  return (
    <div className="blog-card-main-container">
      {Array(5)
        .fill()
        .map((_, index) => {
          return (
            <div key={index} className="blog-card-container">
              <div className="blog-card-container-image"></div>

              <div className="blog-card-container-date">
                Posted on: 24/07/2023
              </div>

              <div className="blog-card-container-title">
                <h2>
                  The Role of AI in Digital Marketing: Enhancing Customer
                  Experience and Efficiency
                </h2>
              </div>

              <div
                className={`blog-card-container-content ${
                  expand ? "expanded" : ""
                }`}
              >
                <span
                  dangerouslySetInnerHTML={{
                    __html: `${
                      !expand
                        ? "Introduction: In this article, we will examine how artificial intelligence (AI) is transforming the field of digital marketing and how it may aid companies in developing stronger relationships with their target markets while maximizing their marketing initiatives. The Redefined Personalization AI&#8217;s capacity to revolutionize tailored client experiences is one of the most important contributions...."
                        : htmlToBeSet
                    }`,
                  }}
                ></span>

                {!expand ? (
                  <AiFillPlusCircle
                    color={"#612d91"}
                    size={30}
                    className="blog-plus-sign"
                    onClick={() => {
                      setExpand(true);
                    }}
                  />
                ) : (
                  <AiFillMinusCircle
                    color={"#612d91"}
                    size={30}
                    className="blog-plus-sign"
                    onClick={() => {
                      setExpand(false);
                    }}
                  />
                )}
              </div>

              <div className="blog-card-container-button">
                <button>
                  Open in new tab <MdOpenInNew color={"#fff"} size={20} />
                </button>
              </div>
            </div>
          );
        })}
    </div>
  );
}
